export default {
  name: "Header",
  data() {
    return {
      logoImg: logoImg,
      cart: [
        {
          img:
            "https://balenciaga.dam.kering.com/m/7ac88a759f20594b/Small-685613W2RA61000_X.jpg?v=3",
          name: " .",
          option: " ",
          many: 1,
          price: 10000,
        },
      ],
      wishlist: [
        {
          img:
            "https://balenciaga.dam.kering.com/m/8d9b17e3ddace31/Small-809489TNO121000_Y.jpg?v=1",
          name: " .",
          option: " ",
          many: 1,
          price: 2000000,
        },
      ],
      activeTab: "login",
      login: {
        email: "",
        password: "",
      },
      guest: {
        gOrderNo: "",
        gEmail: "",
        gName: "",
      },
      findPassword: {
        fpEmail: "",
      },
      autoLogin: false,
    };
  },
  methods: {
    //  
    removeItem(index) {
      // Remove the item at the given index
      this.cart.splice(index, 1);
    },
    //    
    async sendEmail() {
      if (!this.findPassword.fpEmail) {
        alert(" .");
        return;
      }

      try {
        const response = await axios.post("/api/send-email", {
          email: this.findPassword.fpEmail,
        });
        if (response.data.success) {
          alert("  .");
        } else {
          alert("  .  .");
        }
      } catch (error) {
        console.error(" :", error);
        alert("  .   .");
      }
    },
    formatPrice(price) {
      return price.toLocaleString();
    },
  },
};
</script>
