<template>
  <div class="joinWrap col-xl-3 com-xs-10 m-auto pt-4 pb-4">
    <h1 class="fw-900"></h1>
    <div class="inputBox">
      <label class="fw-500"></label>
      <input type="text" name="id" v-model="joinNeeds.id" placeholder="" />
      <input
        type="password"
        name="password"
        v-model="joinNeeds.password"
        placeholder=""
      />
      <input
        type="password"
        name="passwordConfirm"
        v-model="joinNeeds.passwordConfirm"
        placeholder=""
      />
    </div>
    <div class="inputBox">
      <input
        type="text"
        name="phone"
        v-model="joinNeeds.phone"
        placeholder="  ex) 01011112222"
      />
      <input type="text" name="name" v-model="joinNeeds.name" placeholder="" />
      <input type="email" name="email" v-model="joinNeeds.email" placeholder="" />
    </div>
    <div class="inputBox">
      <div class="address d-flex justify-content-between align-items-start">
        <input type="text" name="address" v-model="joinNeeds.address" />
        <button type="button" class="fw-700" @click="openPostcode()"></button>
      </div>
      <input type="text" name="addaddress" v-model="joinNeeds.addaddress" />
    </div>
    <div class="text-left">
      <label class="fw-500"></label>
      <div class="d-flex align-items-center">
        <input type="checkbox" name="apply1" v-model="joinNeeds.apply1" />
        <label for="apply11"  class="fw-300"><button type="button" data-bs-toggle="modal" data-bs-target="#apply1" class="fw-300"> </button>  .</label>
      </div>
      <div class="d-flex align-items-center">
        <input type="checkbox" name="apply2" v-model="joinNeeds.apply2" />
        <label for="apply12" class="fw-300"><button type="button" data-bs-toggle="modal" data-bs-target="#apply2" class="fw-300"> </button>  .</label>
      </div>
      <div class="d-flex align-items-center">
        <input type="checkbox" name="applyall" v-model="selectAll" />
        <label for="applyall" class="ml-2 fw-300"></label>
      </div>
    </div>
    <button type="button" class="btn-primary w-100"></button>
  </div>

  <!-- Modal -->
<div class="modal fade" id="apply1" tabindex="-1" aria-labelledby="apply1Label" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <div class="modal-header">
        <img :src="logoImg" alt="">
      </div>
      <div class="modal-body">
      <label for=""  class="fw-900"></label>
        <textarea rows="" cols="" readonly></textarea>
      </div>
      <div class="modal-footer single">
        <button type="button" class="btn btn-primary" data-bs-dismiss="modal"> .</button>
      </div>
    </div>
  </div>
</div>

<!-- Modal -->
<div class="modal fade" id="apply2 " tabindex="-1" aria-labelledby="apply2Label" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <div class="modal-header">
        <img :src="logoImg" alt="">
      </div>
      <div class="modal-body">
      <label for="" class="fw-900"></label>
        <textarea rows="" cols="" readonly></textarea>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-primary" data-bs-dismiss="modal"> .</button>
      </div>
    </div>
  </div>
</div>
</template>
<script>
/* eslint-disable vue/multi-word-component-names */
export default {
  name: "JoinPage",
  data() {
    return {
        logoImg:
        "https://www.balenciaga.com/on/demandware.static/-/Sites/default/dw983514c8/images/logo/BAL/logo.svg",
      joinNeeds: {
        id: "",
        password: "",
        passwordConfirm: "",
        phone: "",
        name: "",
        email: "",
        address: "",
        addaddress: "",
        postNumber: "",
        apply1: false,
        apply2: false,
      },
    };
  },
  methods: {
    openPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          this.joinNeeds.address = `(${data.zonecode}) ${data.roadAddress}`;
        },
      }).open();
    },
  },
  computed: {
    // ``     
    selectAll: {
      get() {
        return this.joinNeeds.apply1 && this.joinNeeds.apply2;
      },
      set(value) {
        this.joinNeeds.apply1 = value;
        this.joinNeeds.apply2 = value;
      },
    },
  },
};
</script>
