<script>
export default {
  name: "",

  data() {
    return {
      product: {
        name: "  .",
        option: "",
        count: 1,
      },
      selectedOption: "",
      cnr: {
        label: ["", ""],
      },
      imagePreviews: [],
      refund: false, //  
      change: false, //  
      refundOption: "",
      changeOption: {
        label: ["A", "B"],
      },
      changeOptionConfirm: "",
      changeWhy: {
        option: "",
        reason: "",
        file: "",
        changeProduct: "",
      },
    };
  },
  methods: {
    selectOption(item) {
      this.selectedOption = item; //   
      this.refund = item === "";
      this.change = item === "";
    },
    selectOption2(item) {
      this.changeOptionConfirm = item; //   
    },
    handleFileUpload(event) {
      const files = Array.from(event.target.files); // Convert the FileList to an array
      this.changeWhy.file = files; // Store the file objects

      // Clear existing previews
      this.imagePreviews = [];

      // Generate preview URLs
      files.forEach((file) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.imagePreviews.push(e.target.result); // Push the image data URL to the array
        };
        reader.readAsDataURL(file);
      });
    },
  },
};
</script>
