            placeholder="  or    ."
          />
        </div>
        <!-- Title Input -->
        <div
          class="inputBox"
          data-aos="fade-up"
          data-aos-duration="1000"
          data-aos-delay="1500"
        >
          <label></label>
          <input
            type="text"
            name="title"
            v-model="inquiry.title"
            placeholder="  ."
          />
        </div>
        <!-- Inquiry Text -->
        <div
          class="inputBox"
          data-aos="fade-up"
          data-aos-duration="1000"
          data-aos-delay="2000"
        >
          <label></label>
          <textarea
            v-model="inquiry.text"
            placeholder="  ."
          ></textarea>
        </div>
        <!-- File Attachment -->
        <div
          class="inputBox"
          data-aos="fade-up"
          data-aos-duration="1000"
          data-aos-delay="2000"
        >
          <label></label>
          <div>
            <div class="fake">
              <input
                type="file"
                name="file"
                accept="image/*"
                @change="handleFileUpload"
                multiple
              />
              <i class="fa-light fa-plus"></i>
            </div>
            <div
              class="fileBucket mt-4"
              v-if="imagePreviews.length > 0"
              data-aos="fade-up"
              data-aos-duration="1000"
              data-aos-delay="100"
            >
              <p class="fw-700"> </p>
              <div
                class="imgWrap d-flex align-items-center justify-content-start gap-1 mt-4"
              >
                <div
                  class="imgBox"
                  v-for="(imgSrc, index) in imagePreviews"
                  :key="index"
                  @mouseover="hoveredImage = index"
                  @mouseleave="hoveredImage = null"
                >
                  <img :src="imgSrc" alt="Uploaded image" />
                  <button v-if="hoveredImage === index" @click="removeImage(index)">
                    <i class="fa-solid fa-trash-can-xmark"></i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Buttons -->
        <div
